.post {
  margin-bottom: 30px;
  &__content {
    padding: 30px; }
  &__heading {
    font-size: 20px;
    line-height: 1; }
  &__meta {
    font-size: 14px;
    line-height: 1.2;
    padding-bottom: 15px; }
  &__date {
    font-size: 16px;
    line-height: 36px; }
  &__category {
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    color: $color_secondary; }
  &__share {
    margin-top: 50px; } }

.post--left {
  @include rmin(992) {
    display: flex;
    .post__image {
      flex: 0 0 $grid_7_12;
      max-width: $grid_7_12; }
    .post__content {
      flex: 0 0 $grid_5_12;
      max-width: $grid_5_12; } } }

.post--single {
  .post__content {
    padding: 0; }
  .post__heading {
    font-size: 30px;
    line-height: 32px;
    margin: 25px 0 7px;
    .link {
      font-size: 17px;
      font-weight: 500; }
    @include r(1199) {
      font-size: 24px; } }
  .post__meta {
    font-size: 16px;
    margin-bottom: 20px; }
  .post__image-back {
    @include rmin(992) {
      height: 400px;
      object-fit: cover; } } }

.sidebar {
  .post {
    position: relative;
    background: $color_secondary;
    margin-bottom: 0;
    &__image-front {
      position: absolute;
      font-size: 0;
      top: 0;
      left: 0;
      height: 100%; }
    &__heading {
      color: $color_t; }
    .link {
      color: $color_t;
      @include r(991) {
        font-size: 16px; }
      @include hover {
        color: $color_secondary; } }
    &__content {
      position: relative;
      padding: 20px;
      background: #fff;
      height: auto;
      border: 1px solid #f7f7f7; }
    &__date {
      font-size: 14px;
      line-height: 1.2; }
    &__category {
      font-family: $font_primary;
      font-size: 14px;
      line-height: 1.2;
      font-weight: 400;
      color: $color_t;
      border-left: 1px solid;
      margin-left: 4px;
      padding-left: 7px;
      text-transform: none; }
    &__image-back {
      width: 100%;
      height: 100%;
      object-fit: cover; } } }

.post--hover {
 // .post__content
 //   transition: opacity .3s
 //   opacity: 0
 //include hover
 // .post__content
 } //   opacity: 1

@include for-touch {
  .post--hover {
    &.active {
      .post__content {
        opacity: 1; } } } }


.tags {
  font-size: 18px;
  line-height: 24px;
  &__item {
    margin-bottom: 5px; }
  &--footer &__item {
    display: inline-block;
    margin-right: 5px; }
  &__link {
    color: #a2a1a1;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 400;
    &:hover {
      color: $color_secondary; } } }
