@include font('ufuture-font', affect-webfont);
@include font('ufuture-font', affect_light-webfont, 300);
@include font('ufuture-font', affect_bold-webfont, 700);

html {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

body {
	background: #FFF;
	font-family: "Open Sans",sans-serif;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.4;
	color: $color_paragraph;
	@include rmin(1400) {
		font-size: 18px;
		line-height: 1.76; } }

p {
	margin-bottom: 15px;
	@include rmin(1400) {
		margin-bottom: 25px; } }

blockquote, .blockquote {
	position: relative;
	font-size: 20px;
	font-weight: 400;
	line-height: 1.55;
	padding-left: 45px;
	margin-top:	30px;
	margin-bottom: 90px;
	&:before {
		content: '';
		width: 3px;
		height: 100%;
		background: $color_secondary;
		position: absolute;
		top: 0;
		left: 0; }
	.page__content & {
		margin-bottom: 100px; }
	@include rmin(1400) {
		font-size: 20px;
		margin-bottom: 80px; }
	@include r(767) {
		font-size: 16px;
		margin-bottom: 80px; } }



cite {
	position: absolute;
	bottom: -60px;
	right: 0;
	display: block;
	text-align: right;
	font-size: 14px;
	font-style: normal;
	font-weight: 300;
	@include rmin(1400) {
		font-size: 16px; }
	@include r(768) {
		padding-left: 30px; } }


h1, .h1 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 40px;
	line-height: 1.2;
	color: $color_t;
	@include r(575) {
		font-size: 26px; } }
h2, .h2 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 38px;
	line-height: 1.2;
	color: $color_t; }
h3, .h3 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 30px;
	line-height: 1.2;
	color: $color_t; }

.img-responsive {
	max-width: 100%;
	height: auto;
	border: none; }

.b-lazy {
	opacity: 0.2;
	transition: opacity 500ms; }

.b-loaded {
	opacity: 1; }

.link {
	font-size: 18px;
	line-height: 1.2;
	text-decoration: none;
	color: $color_secondary; }



.i-prop {
	font-style: normal;
	font-weight: normal; }

.special {
	@include rmin(992) {
		.page__content > * {
			margin-left: 16.66667%;
			margin-right: 16.66667%; }
		.page__content blockquote {
			margin-left: 0;
			margin-right: 0; }
		.page__content .full-img {
			margin-left: 0;
			margin-right: 0; }
		.page__content .full-video {
			margin-left: 0;
			margin-right: 0; } }
	@include r(991) {
		.page__article {
		 padding: 50px 0 15px; } } }


.page__content {
	padding-top: 30px;
	h1,h2,h3,h4,h5,h6 {
		margin-bottom: 15px; } }

.margin-plus-450 {
	width: 100%; }

.ul-style {
	padding-bottom: 20px;
	font-weight: 400; }

.li-style {
		list-style: disc;
		font-weight: 300;
		list-style-position: outside;
		padding-top: 5px;
		padding-bottom: 5px;
		margin-left: 20px; }

.post-link {
	font-weight: 500;
	color: #292929;
	text-decoration: none;
	border-bottom: 1px solid; }

.img-box {
	overflow: hidden;
	img {
		transition: all 6500ms;
		transform: scale(1.08);
		opacity: 1; }
	.loaded & img {
		transform: scale(1);
		opacity: 1; } }
