.inline-list {
  font-family: $font_secondary;
  font-weight: 400;
  &__item {
    display: inline-block;
    @include r(767) {
        display: block; }
    &:before, &:after {
      content: '';
      position: relative;
      width: 2px;
      height: 0.84em;
      background: $color_secondary;
      display: inline-block;
      vertical-align: middle;
      margin:  0px 20px 0px; }
    &:last-child:after {
      display: none; }
    @include rmin(768) {
      &:before {
        display: none; } }
    @include r(767) {
      &:before {
        margin-left: 0; }
      &:after {
        display: none; } } }
  &__link {
    display: inline-block;
    vertical-align: middle;
    color: $color_t;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .3s;
    &.active {
      pointer-events: none;
      color: $color_secondary; }
    @include hover {
      color: $color_secondary; }
    @include r(768) {
      font-size: 20px; } } }
