.news {
  position: relative;
  .frontpage & {
    background: $color_light-gray;
    margin-top: -60px;
    z-index: 1; }
  .post__text, .post__title {
    transform: translateX(-15px);
    opacity: 0; }
  &__blocks {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap; }
  &__col {
    flex: 0 0 50%;
    @include r(991) {
      flex: 0 0 100%; } }
  &__item {
    position: relative;
    @include hover {
      .news__content {
        opacity: 1; }
      .post__text, .post__title {
        opacity: 1;
        transform: translateX(0); } } }
  &__content {
    position: relative;
    padding: 35px;
    background: $color_secondary;
    opacity: 0;
    overflow: hidden;
    transition: opacity .3s;
    z-index: 1;
    @include r(1199) {
      position: relative;
      padding: 35px;
      background: $color_secondary;
      opacity: 0.85;
      overflow: hidden;
      transition: opacity .3s;
      z-index: 4; } }

  &__face {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden; }
  &__background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-position: 50% 50%;
    background-repeat: none;
    background-size: cover;
    z-index: 0; }
  &__aside-h {
    position: relative;
    font-size: 20px;
    line-height: 27px;
    color: $color_t;
    font-family: $font_secondary;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 30px;
    margin-bottom: 50px;
    &--first {
      margin-top: 9px; } }
  &__footer {
    padding-top: 20px; } }

@include for-touch {
  .news__face {
    z-index: 2; }
  .news__item {
    .post__text, .post__title, .read-more {}
    &.active {
      .news__face {
        z-index: 1; }
      .news__content {
        opacity: 1; }
      .post__text, .post__title, .read-more {
        opacity: 1;
        transform: translateX(0); } } } }
